<template>
  <div class="dialog-demo">
    <div class="des">Dialog</div>
    <m-button text=" alert" type="primary" @click.native="showAlert"></m-button>
    <m-button text=" confirm" type="primary" @click.native="showConfirm"></m-button>
    <m-button text=" prompt" type="primary" @click.native="showPrompt"></m-button>
    
  </div>
</template>
<script>
import MButton from '../components/MButton/index.vue'
import MessageBox from '../components/Msgbox/index.js'

export default {
  components: {
    MButton
  },
  methods: {
    showAlert: function() {
      MessageBox.alert('','')
    },
    showConfirm: function() {
      MessageBox.confirm('','')
    },
    showPrompt: function() {
      MessageBox.prompt('').then(({value, action}) => {
        MessageBox.alert(value)
      })
    }
  }
}
</script>
<style>
.des {
  text-align: center;
  font-size: 18px;
}
.dialog-demo{
  padding:0 30px;
}
</style>



