<script>
import MButton from '../components/MButton/index.vue'
import TChecklist from '../components/Checklist/index.vue'
export default {
  data() {
    return {
    };
  },
  components: {
    TChecklist
  },
  created() {
    this.options1 = {
      hasChecked: ['A1'], // optionListvalue
      optionList: ['A1', 'B1', 'C1']
    }
    this.options3 = {
      hasChecked: ['A3'], // optionListvalue
      optionList: ['A3', 'B3', 'C3', 'D3']}
    this.options2 = {
      hasChecked: ['','B4'], // optionListvalue
      optionList:
      [{
        label: '',
        value: 'F',
        disabled: true
      },
      {
        label: '',
        value: '',
        disabled: true
      },
      {
        label: 'A4',
        value: 'A'
      },
      {
        label: 'B',
        value: 'B4'
      }]
    };
  },
  methods: {
    getValue() {
      console.log('111111111', this.options1.hasChecked) // 
      console.log('222222222', this.options2.hasChecked)
      console.log('333333333', this.options3.hasChecked)
    }
  }
};
</script>
<style>
.des {
  text-align: center;
  font-size: 18px;
}
.dialog-demo{
  padding:0 30px;
}
</style>



