<template>
  <div class="tabBar-demo">
    <div class="des">tabbar</div>
    <t-tabbar v-bind:tabs='tabs' v-on:tabchange="tabChange"></t-tabbar> 
  </div>
</template>
<script>
import TTabbar from '../components/Tabbar/index.vue'
export default {
  data () {
    return {
      tabs:{
        // fixed: false, // false,true
        isSelect: 0, // 0
        isSelectCon: ['', '', ''], // 
        tabList: ['', '', ''] // tabtitle
      }
    }
  },
  components: {
    TTabbar
  },
  methods: {
    tabChange: function (index) {
      let j = parseInt(index) // 
      this.tabs.isSelectCon.splice(j, 1, 'post'+j) // 
    }
  }
}
</script>
<style>
.toast-demo{
  padding:0 30px;
}
.des {
  text-align: center;
  font-size: 18px;
}
</style>



